import { Link } from "gatsby";
import * as React from "react";
import styled from "styled-components";
import { ContactForm, FormType } from "../helpers/contactForm";
import { Footer } from "../helpers/footer";
import {
  FormSection,
  FormSectionTitle,
  FormText,
  FormTitle,
} from "../helpers/form-sections";
import { MainHeader } from "../helpers/header";
import { SeoComponent } from "../helpers/seoComponent";
import Chevron from "../images/chevron.svg";

// markup
const ContactPage = () => {
  return (
    <SeoComponent>
      <main>
        <MainHeader dark />
        <FormSection>
          <div>
            <FormTitle>Contact</FormTitle>
            <FormText>
              We drive access and transparency across agricultural commodity
              markets, enabling our users to capture more value. Interested to
              learn more?
            </FormText>

            <Link to="/press" style={{ textDecoration: "none" }}>
              <ContactLink>
                <StyledFormSectionTitle>Press inquiry </StyledFormSectionTitle>
                <IconWrapper>
                  <Chevron />
                </IconWrapper>
              </ContactLink>
            </Link>

            <FormText>
              For Vosbor's resources for journalists and more information,
              please contact our PR team.
            </FormText>

            <Link to="/careers" style={{ textDecoration: "none" }}>
              <ContactLink>
                <StyledFormSectionTitle>Careers</StyledFormSectionTitle>
                <IconWrapper>
                  <Chevron />
                </IconWrapper>
              </ContactLink>
            </Link>

            <FormText>
              Interested in helping us build the sustainable trade system that
              can meet the nutrition challenges of the future? We would love to
              hear from you.
            </FormText>
          </div>
          <ContactFormWrapper>
            <ContactForm type={FormType.info}></ContactForm>
          </ContactFormWrapper>
        </FormSection>
        <Footer />
      </main>
    </SeoComponent>
  );
};

export default ContactPage;

const ContactLink = styled.div`
  display: flex;
  margin-top: 40px;
  align-items: center;
  gap: 5px;
`;

const IconWrapper = styled.div`
  position: relative;
  width: 16px;
  height: 16px;

  & svg {
    position: absolute;
  }
`;

const StyledFormSectionTitle = styled(FormSectionTitle)`
  margin: 0;

  &:hover {
    color: #717178;
  }
`;

const ContactFormWrapper = styled.div`
  margin-top: 40px;
`;
